.btn {
  color: var(--light-color);
  padding: 1.5vw 4vh;
  width: 23vw;
  height: 6vw;
  border-radius: 3.5vw;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-size: 0.9vw;
  font-weight: 600;
  line-height: 1.5;
  transition: all 0.4s;
  svg {
    fill: var(--light-color);
    flex-shrink: 0;
    width: 2vw;
    height: 2vw;
  }
}

.btn--primary {
  background-color: var(--green-color);
  &:hover {
    background-color: #21e27c;
  }
}

.btn--secondary {
  background-color: var(--orange-color);
  &:hover {
    background-color: #fd9c49;
  }
}

@include tablet {
  .btn {
    padding: 30px 75px;
    width: 500px;
    height: 80px;
    border-radius: 40px;
    font-size: 15px;
    svg {
      width: 40px;
      height: 40px;
    }
  }
}

.benefits__btn {
  display: none;
  background-color: var(--orange-color);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  svg {
    width: 20px;
    height: 20px;
    fill: var(--light-color);
  }
}

@include tablet {
  .benefits__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
  }
  .btn--primary {
    margin-bottom: 20px;
  }
}

@include mobile {
  .btn {
    padding: 30px 40px;
    width: 100%;
    font-size: 11px;
    height: 30px;
  }
}
